.App {
	text-align: center;
}

* {
	margin: 0;
}

body {
	background-color: black;
	color: white;
}

.app__body {
	width: 100%;
	height: 100%;
	display: flex;
	justify-content: center;
	margin-top: ;
}

.app__container {
	width: 1024px;
	display: flex;
	height: 100%;
	padding-left: 48px;
	padding-right: 48px;
	padding-top: 36px;
}

.App-logo {
	height: 40vmin;
	pointer-events: none;
}

@media (prefers-reduced-motion: no-preference) {
	.App-logo {
		animation: App-logo-spin infinite 20s linear;
	}
}

.App-header {
	background-color: #282c34;
	min-height: 100vh;
	display: flex;
	flex-direction: column;
	align-items: center;
	justify-content: center;
	font-size: calc(10px + 2vmin);
	color: white;
}

.App-link {
	color: #61dafb;
}

@keyframes App-logo-spin {
	from {
		transform: rotate(0deg);
	}
	to {
		transform: rotate(360deg);
	}
}
